.root {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.topBar {
  display: flex;
  justify-content: space-between;
  align-self: stretch;
}
.topBar :first-child {
  font-size: 1.5rem;
}

.close {
  align-self: flex-end;
}

.next {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  max-width: 85vw;
  padding: 2rem;
  background-color: rgba(78, 78, 78, 0.95);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 2.25rem;
}
.container > * + * {
  margin-block-start: 2rem;
}