.tile {
  aspect-ratio: 1;
  min-width: 18px;
  min-height: 18px;
  overflow: hidden;
  background-color: #4e4e4e;
  border: solid 2px #f8f8f8;
  margin: 0.5vmin;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 7vmin;
}

.Green {
  background-color: #448844;
}

.Yellow {
  background-color: #888844;
}

.Grey {
  background-color: #343434;
}